<script>
  import { onMount } from "svelte";
  import * as ls from "local-storage";
  export let id;
  export let titre;
  export let author;
  export let cover;
  export let description;
  export let file;
  export let prix;
  export let bought = false;

  onMount(() => {
    if (ls.get("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu")) {
      if (
        ls
          .get("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu")
          .livres.find((livre) => livre.id === id)
      ) {
        bought = true;
      }
    }

    paypal
      .Buttons({
        createOrder: (data, actions) => {
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  value: prix,
                },
              },
            ],
          });
        },
        onApprove: (data, actions) => {
          return actions.order.capture().then((details) => {
            console.log("Transaction done by " + details.payer.name.given_name);
            console.log(details.payer);

            bought = true;

            if (!ls.get("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu")) {
              ls.set("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu", {
                livres: [{ id, file }],
              });
            } else {
              let livres = ls.get("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu").livres;
              livres.push({ id, file });
              ls.set("39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu", { livres });
            }
          });
        },
      })
      .render(`#${id}`);
  });
</script>

<style>
</style>

<div
  class="rounded-md shadow-sm bg-gray-50 flex flex-col sm:flex-row py-4 px-4 relative"
  style="outline: 1px dashed rgba(0, 0, 0, .1); outline-offset: -2px;">
  <div
    class="shadow-sm rounded-md bg-brand-strong-green px-4 py-2 absolute left-3 top-3"
    style="outline: 1px dashed rgba(0, 0, 0, .1); outline-offset: -4px;">
    <h1 class="stylized-white text-2xl">€{prix}</h1>
  </div>

  <img
    class="w-full sm:h-64 lg:h-80 sm:w-auto object-cover"
    src={cover}
    alt={cover} />

  <div class="sm:pl-4 flex flex-col justify-between w-full">
    <div class="mt-4 sm:mt-0">
      <h1 class="flex items-center text-lg">
        <h1 class="stylized">{titre}</h1>
      </h1>
      <h1 class="mt-4 text-brand-weak-green">{author}</h1>
    </div>
    <div class="text-gray-600 mt-4">{description}</div>

    {#if bought}
      <a href={file} class="btn btn-primary mt-4" style="width: 100%;">
        <i class="fab fa-download mr-2 text-brand-weak-green" />
        Telecharger
      </a>
    {:else}
      <div class="mt-4" style="width: 100%;" {id} />
    {/if}
  </div>
</div>
