<script>
  import SectionHeading from "./../shared/SectionHeading.svelte";
  import YoutubeVideo from "../shared/YoutubeVideo.svelte";
  import ProgramCard from "../shared/ProgramCard.svelte";
  import Container from "./../shared/Container.svelte";
  import Contact from "../components/Contact.svelte";
  import * as animateScroll from "svelte-scrollto";
  import { Circle } from "svelte-loading-spinners";
  import Layout from "../Layout/Layout.svelte";
  import Hero from "../components/Hero.svelte";
  import { link } from "svelte-spa-router";
  import * as ls from "local-storage";
  import { onMount } from "svelte";
  import axios from "axios";

  const programs = [
    {
      id: 1,
      horaire: "Du Lundi au Dimanche : 11h30 et 21h00",
      nom: "Facebook Live",
      fa: "fab fa-facebook mr-3 fa-2x",
      couleur: "#3b5998",
      description:
        "Du lundi au dimanche nous somme sur notre page facebook pour notre culte journalier.",
      cta: "Voir Notre Page",
      url:
        "https://web.facebook.com/Proph%C3%A8te-Philippe-Mukoma-Wetu-590548967798711",
    },
    {
      id: 2,
      horaire: "Du Lundi au Dimanche : 11h30 et 21h00",
      nom: "Rendez-vous Zoom",
      fa: "fas fa-video mr-3 fa-2x",
      couleur: "#2D8CFF",
      description:
        "Du lundi au dimanche rejoignez nous pour notre rendez-vous sur zoom.",
      cta: "Suivre le lien",
      url:
        "https://web.facebook.com/Proph%C3%A8te-Philippe-Mukoma-Wetu-590548967798711",
    },
  ];

  let dernieresVideos;

  onMount(async () => {
    const addDays = (date, days) => {
      var result = new Date(date);
      result.setDate(result.getDate() + days);
      return result;
    };

    if (!ls.get("dernieresVideos")) {
      let res = await axios.get(
        "https://www.googleapis.com/youtube/v3/search?key=AIzaSyArtiELyTNXTtRauX0yudqiMCan-Wbz1ac&channelId=UCNDSjjFloW0vPaMJa5aYOqw&part=snippet,id&order=date&type=video&maxResults=4"
      );
      dernieresVideos = res.data.items;

      ls.set("dernieresVideos", {
        expires: addDays(new Date(), 2),
        videos: dernieresVideos,
      });
    } else if (new Date().toISOString() >= ls.get("dernieresVideos").expires) {
      let res = await axios.get(
        "https://www.googleapis.com/youtube/v3/search?key=AIzaSyArtiELyTNXTtRauX0yudqiMCan-Wbz1ac&channelId=UCNDSjjFloW0vPaMJa5aYOqw&part=snippet,id&order=date&type=video&maxResults=4"
      );
      dernieresVideos = res.data.items;

      ls.set("dernieresVideos", {
        expires: addDays(new Date(), 2),
        videos: dernieresVideos,
      });
    } else {
      dernieresVideos = ls.get("dernieresVideos").videos;
    }
  });
</script>

