<script>
  import SectionHeading from "./../shared/SectionHeading.svelte";
  import YoutubeVideo from "../shared/YoutubeVideo.svelte";
  import Container from "./../shared/Container.svelte";
  import * as animateScroll from "svelte-scrollto";
  import { Circle } from "svelte-loading-spinners";
  import Layout from "../Layout/Layout.svelte";
  import * as ls from "local-storage";
  import { onMount } from "svelte";
  import axios from "axios";
  import MessageCard from "../shared/MessageCard.svelte";

  let messagesYT;

  let messages = [
    {
      id: "message-01",
      titre: "Mon Jour de Delivrance",
      author: "Prophète Philippe Mukoma Wetu",
      cover: "./39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu/mon-jour-de-delivrance.png",
      description:
        "Ce manuel a été conçu de manière très pratique pour vous accompagner jour après jour dans cette quête vers votre délivrance totale. D'où, il a été parfaitement délimité pour...",
      file: "./39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu/mon-jour-de-delivrance.pdf",
      prix: "150",
    },
    {
      id: "message-02",
      titre: "Prophète J'ai Un Amant Dans Mes Songes",
      author: "Prophète Philippe Mukoma Wetu",
      cover:
        "./39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu/prophete-j-ai-un-amant-dans-mes-songes.png",
      description:
        "Quand un homme fort et bien armé garde sa maison, ses richesses sont en sécurité. Il n’a pas peur, parce qu’il est bien armé. Mais quand un homme plus fort que lui arrive...",
      file:
        "./39Dhs&LrP4hKgpfFLXuExA3y9B5U7fVu/prophete-j-ai-un-amant-dans-mes-songes.pdf",
      prix: "50",
    },
  ];

  onMount(async () => {
    animateScroll.scrollToTop();

    const addDays = (date, days) => {
      var result = new Date(date);
      result.setDate(result.getDate() + days);
      return result;
    };

    if (!ls.get("messages")) {
      let res = await axios.get(
        "https://www.googleapis.com/youtube/v3/search?key=AIzaSyArtiELyTNXTtRauX0yudqiMCan-Wbz1ac&channelId=UCNDSjjFloW0vPaMJa5aYOqw&part=snippet,id&order=date&type=video&maxResults=40"
      );
      messagesYT = res.data.items;

      ls.set("messages", {
        expires: addDays(new Date(), 2),
        videos: messagesYT,
      });
    } else if (new Date().toISOString() >= ls.get("messages").expires) {
      let res = await axios.get(
        "https://www.googleapis.com/youtube/v3/search?key=AIzaSyArtiELyTNXTtRauX0yudqiMCan-Wbz1ac&channelId=UCNDSjjFloW0vPaMJa5aYOqw&part=snippet,id&order=date&type=video&maxResults=6"
      );
      messagesYT = res.data.items;

      ls.set("messages", {
        expires: addDays(new Date(), 2),
        videos: messagesYT,
      });
    } else {
      messagesYT = ls.get("messages").videos;
    }
  });
</script>

<style>
</style>

<svelte:head>
  <title>Messages | Eglise La Restauration des Familles</title>
</svelte:head>

<Layout>
  <div
    class="h-screen overflow-hidden bg-brand-darker-black relative"
    style="background-image: url(./images/bg-02.jpg); background-size: cover; background-position:top; background-blend-mode: overlay; background-attachment: fixed; outline: 1px dashed rgba(0, 0, 0, .1); outline-offset: -5px;">
    <div
      class="w-full h-full flex items-center justify-center"
      style="background: rgba(31, 41, 55,.5);">
      <Container>
        <div class="flex items-center justify-center mb-8">
          <img class="w-32" src="./images/logo.png" alt="Logo" />
        </div>
        <h1 class="stylized-white text-3xl sm:text-5xl md:text-7xl text-center">
          MESSAGES VIDEOS & SEVICES
        </h1>
        <button
          on:click={() => animateScroll.scrollTo({ element: '#messages' })}
          class="flex items-center justify-center absolute bottom-16 md:bottom-6 left-0 right-0 w-full">
          <i class="fas fa-arrow-down animate-bounce fa-2x text-gray-100" />
        </button>
      </Container>
    </div>
  </div>

  <Container>
    <!-- <SectionHeading text1="MESSAGES AUDIO" id="messages" /> -->

    <!-- <div class="grid grid-cols-1 lg:grid-cols-2 gap-4">
      {#each messages as message (message.id)}
        <MessageCard {...message} />
      {/each}
    </div> -->
  </Container>

  <Container>
    <SectionHeading text1="DE NOTRE CHAINE YOUTUBE" id="messages-youtube" />

    {#if messagesYT}
      <div class="grid grid-cols-1 sm:grid-cols-2 gap-x-6 gap-y-6">
        {#each messagesYT as video}
          {#if video.id.videoId}
            <YoutubeVideo
              videoID={video.id.videoId}
              title={video.snippet.title} />
          {/if}
        {:else}
          <div class="h-72 flex items-center justify-center">
            <h1 class="text-gray-800 text-2xl text-center ">
              😟 Probleme de Connexion, veuillez actualiser la page!
            </h1>
          </div>
        {/each}
        <a
          href="https://www.youtube.com/channel/UCNDSjjFloW0vPaMJa5aYOqw"
          target="_blank"
          class="btn btn-primary col-span-full"
          style="width: 100%; outline-color: #bb000050">Voir l'integralité sur
          Youtube
          <i class="fab fa-youtube ml-3 text-xl" style="color: #bb0000" />
        </a>
      </div>
    {:else}
      <div class="h-72 flex items-center justify-center">
        <Circle size="100" color="#bb0000" unit="px" />
      </div>
    {/if}
    <div class="pb-48" />
  </Container>
</Layout>
