<script>
  import { slide } from "svelte/transition";
  import { link } from "svelte-spa-router";

  let menuVisible = false;
  let toggleMenu = () => {
    menuVisible = !menuVisible;
  };
  let hideMenu = () => {
    menuVisible = false;
  };

  export let showOnlyHome = false;
</script>

<div
  class="block lg:hidden px-4 pt-2 pb-1 rounded-md shadow-sm bg-gray-800 relative"
  style="z-index:999;">
  <button class="focus:outline-none" on:click={toggleMenu}>
    <svg
      xmlns="http://www.w3.org/2000/svg"
      class="w-6 h-5 stroke-current text-gray-300"
      viewBox="0 0 30 21">
      <defs>
        <style>
          .abc {
            fill: none;
            stroke-linecap: round;
            stroke-linejoin: round;
            stroke-width: 3px;
          }
        </style>
      </defs>
      <g transform="translate(-3 -7.5)">
        <path class="abc" d="M4.5,18h27" />
        <path class="abc" d="M4.5,9h27" />
        <path class="abc" d="M4.5,27h27" />
      </g>
    </svg>
  </button>

  {#if menuVisible}
    <div
      transition:slide={{ duration: 500 }}
      class={`text-gray-300 absolute mt-12 top-0 right-0 w-56 flex flex-col px-3 pt-4 pb-2 rounded-md shadow-md bg-gray-800 border-t-4 border-brand-weak-yellow`}>
      <a class="mb-5" on:click={hideMenu} use:link href="/livres">Livres</a>
      <a class="mb-5" on:click={hideMenu} use:link href="/messages">Messages</a>
      <a class="mb-5" on:click={hideMenu} use:link href="/dons">Dons</a>

      {#if !showOnlyHome}
        <a class="mb-5" on:click={hideMenu} href="#contact">Contact</a>
      {/if}
    </div>
  {/if}
</div>
