<script>
  export let id;
  export let horaire = "Du Lundi au Dimanche: 11h30 & 21h00";
  export let nom = "Facebook Live";
  export let fa = "fab fa-facebook mr-3 fa-2x";
  export let description =
    "Du lundi au dimanche nous somme sur notre page facebook pour notre culte journalier.";
  export let cta = "Voir Notre Page";
  export let url =
    "https://web.facebook.com/Proph%C3%A8te-Philippe-Mukoma-Wetu-590548967798711";
  export let couleur = "#3b5998";
</script>

<div class="hidden">{id}</div>

<div
  class="overflow-hidden shadow-md rounded-md"
  style={`background-image: url(./images/faisceau.svg);`}>
  <div
