<script>
  import { Circle } from "svelte-loading-spinners";
  import { fly } from "svelte/transition";
  export let videoID;
  export let title;

  let showTitle = true;
  let loaded = false;
  let iframeVideo;

  const handleOnLoad = () => {
    loaded = true;
  };

  const handleClick = () => {
    if (showTitle) {
      showTitle = false;
    } else {
      showTitle = true;
      setTimeout(() => {
        showTitle = false;
      }, 4000);
    }
  };
</script>

<div class="rounded-md overflow-hidden shadow-sm h-52 md:h-72 lg:h-80 relative">
  {#if showTitle}
    <div
      in:fly={{ y: -15 }}
      out:fly={{ y: -15 }}
      class="absolute left-0 top-0 w-full h-16 px-1 py-1 z-30">
      <h1
        on:click={handleClick}
        style="background-color: #bb0000"
        class="text-gray-700 bg-gray-50 w-full h-full text-xs uppercase tracking-wider flex items-center rounded-md shadow-md text-center px-2 py-2">
        <i class="fab text-3xl fa-youtube mr-2 text-gray-100" />
        <div class="block sm:hidden text-gray-100">
          {title.length < 30 ? title : title.substring(0, 35) + '...'}
        </div>
        <div class="hidden sm:block text-gray-100">
          {title.length < 55 ? title : title.substring(0, 55) + '...'}
        </div>
      </h1>
    </div>
  {/if}

  <div
    class:hidden={!loaded}
    class="overflow-hidden shadow-sm h-52 md:h-72 lg:h-80 relative z-10">
    <iframe
      on:click={handleClick}
      on:load={handleOnLoad}
      title="video"
      width="100%"
      height="100%"
      src={`https://www.youtube.com/embed/${videoID}`}
      frameborder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen />
  </div>

  <div
    class:hidden={loaded}
    class="overflow-hidden bg-gray-900 shadow-sm h-52 md:h-72 lg:h-80 relative z-10 pt-12 flex items-center justify-center">
    <Circle size="50" color="#bb0000" unit="px" />
  </div>
</div>
