<script>
  import Container from "../shared/Container.svelte";
  import MobileMenu from "./MobileMenu.svelte";
  import { link } from "svelte-spa-router";
  import { onMount } from "svelte";
  export let showOnlyHome = false;

  onMount(() => {
    window.onscroll = (e) => {
      scrollFunction();
    };

    const scrollFunction = () => {
      let innerHeader = document.getElementById("inner-header");
      let header = document.getElementById("header");

      if (
        document.body.scrollTop > 40 ||
        document.documentElement.scrollTop > 40
      ) {
        header.classList.add("bg-gray-800");
        // header.classList.add("text-gray-300");
        // header.classList.remove("text-gray-700");
        header.classList.add("shadow-sm");
      } else {
        header.classList.remove("bg-gray-800");
        // header.classList.add("text-gray-700");
        // header.classList.remove("text-gray-300");
        header.classList.remove("shadow-sm");
      }
    };
  });
</script>

<div
  id="header"
  class="navbar bg-transparent text-gray-300 fixed top-0 left-0 w-full z-50 transition-all
    duration-250">
  <Container>
    <div
      id="inner-header"
      class="h-16 flex justify-between items-center transition-all duration-250">
      <a class="nav-link flex items-center justify-center" href="#home">
        <img src="./images/logo.png" alt="Logo" class="w-6 mr-2" />
        Acceuil
      </a>
      <!--  -->
      {#if !showOnlyHome}
        <div class="hidden lg:flex">
          <a class="nav-link mr-6" use:link href="/livres">Livres</a>
          <a class="nav-link mr-6" use:link href="/messages">Messages</a>
          <a class="nav-link mr-6" use:link href="/dons">Dons</a>
        </div>
      {/if}

      <div class="lg:flex lg:items-center">
        {#if !showOnlyHome}
          <a class="nav-link hidden lg:block mr-8 lg:mr-0" href="#contact">
            Contactez-nous
          </a>
        {/if}
        <MobileMenu {showOnlyHome} />
      </div>
    </div>
  </Container>
</div>
